    width: 520px;
    margin: 0 auto;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .non-fluid-container {
    width: 750px;
    margin: 0 auto;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .non-fluid-container {
    width: 900px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .non-fluid-container {
    width: 1000px;
    margin: 0 auto;
  }
}
